@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  border: 0;
  padding: 0;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 10px;
}

body {
  background: #3f3f3f;
  
}


.header{
    background-color:rgb(156, 66, 1);
    height: 8rem;
    width:100%;
    display:flex;
    justify-content:space-around;
    align-items: center;

}

.header__title{
    color:white;
    font-size: 5rem;
    
    
}

.header__card{
    
    width:23rem;
    height:5rem;
    border-radius: 1000rem;
    background-color: rgb(85, 35, 0);
    text-align: center;
    color: white;
    font-size:large;
    line-height: 5rem;
    font-weight: 900;
}

.header__image{
    width: 100%;
    height:30rem;
    -webkit-clip-path: polygon(0 0 ,100% 0, 100% 80%,0 100% );
            clip-path: polygon(0 0 ,100% 0, 100% 80%,0 100% );
}
.cartbutton
{
    width:20rem;
    height: 4.5rem;
    border-radius: 100rem;
    color: white;
    background-color: rgb(97, 40, 0);
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: center;
    
}
.cartbutton:hover{
    transform: scale(1.1);
}

.carticon{
    
    width: 2.5rem;
    height:100%;
    
}
.carttext{
    
    font-weight: bolder;
    font-size:large ;
}

.cart-items-count
{
    font-weight: bolder;
    font-size: 120%;
    background-color: rgb(150, 63, 1);
    width: 3rem;
    border-radius: 100px;
    padding: .2rem;
}
.meals{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width:50vw;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    

}
.meals__description{
    justify-content: space-between;
    align-items: flex-start;
}
.meals__quantity
{
    justify-content: space-evenly;
    align-items: center;
}
.meals__quantitybox{
    border:.02rem solid;
    width:3rem;
    border-radius: 0.3rem;
    margin-left: 1rem;

}
.meals__quantity__add,.meals__quantity__input{
    display: block;
}
.meals__quantity__add{
    font-size: large;
    background-color: chocolate;
    width: 100%;
    height: 2.5rem;
    border-radius:100rem ;
    margin:0.5rem;
    transition:all 0.2s ease-out;
}
.meals__quantity__add:hover{
    transform: scale(1.1);
}
label{
    font-weight: bolder;
    font-size:120%;
}
.card{
   
    padding: 1.5rem;
    border-radius: 1.5rem;
    
    
    
}
.mealscard{
    position:absolute;
    
    z-index: 1;
    background-color: white;
    animation: moveUp 1s ease-in forwards;
    
}

@keyframes moveUp {
    0%{
        transform:translateY(3rem) translateX(42%);
    }

    100%{
        transform: translateY(0) translateX(42%);
    }
    
}
.button{
    width:-webkit-max-content;
    width:max-content;
    padding:0.7rem 1.5rem;
    border-radius: 10rem;
    font-size: large;
    margin: 1rem 0.5rem;
}
.button:hover{
    transform: scale(1.1);
}
.cart-item-container
{
    display: flex;
    justify-content: space-between;
    width:100%;
    height:5rem;
}
.cart-item-specifics
{
    justify-content: space-between;
}
.cart-item-price{
    color: chocolate;
    font-size: large;
    width: 9rem;
    display: inline-block;
}

.cart-item-quantity{
    
    color: gray;
    margin-left: 8rem;
    border:solid rgba(0, 0, 0, 0.205) 1.2px;
    border-radius: .4rem;
    padding: 0.01rem 0.3rem;
    font-size: large;
}
.cart-item-btn{
    width:4rem;
    height: 2.5rem;
    border: solid rgb(210, 105, 30) 0.15rem;
    border-radius: 0.5rem;
    color:chocolate;
    background-color: transparent;
    font-size: large;
    

}
.cart-item-btn:hover{
    transform: scale(1.2);
}
.btns{
    
    width: 15%;
    display: flex;
    justify-content: space-around;
    align-items:flex-end;

}

.cart-background{
    background: rgba(0,0 , 0, 0.8);
    position: fixed;
    z-index: 10;
    width: 100%;
    height:100%;
    
  }

  .meals-cart-card
  {
    background-color: white;
    position: fixed;
    z-index:100;
    width:50%;
    transform: translateX(47%);
    top:20%;
  }


  .amount_container{
    display: flex;
    justify-content: space-between;
  }

  .part{
    display: inline-block;
  }

  .button-close{
    background-color: white;
    border:solid 1px;
    color: chocolate;
  }

  .button-order{
    background-color: chocolate;
    border:solid 1px;
    color: white;
  }
